import React from "react";
import { useStaticQuery, graphql } from "gatsby";
import Header from "./header";
import './layout.css';

const Layout = ({children}) => {
    const data = useStaticQuery(graphql`
        query SiteTitleQuery {
            site {
                siteMetadata {
                    title
                }
            }
        }
    `)
    
    return (
        <>
            <Header siteTitle={data.site.siteMetadata.title}></Header>
            <div
                style={{
                    margin: `0 auto`,
                    maxWidth: 960,
                    padding: `0 1.0875rem 1.45rem`,
                }}
            >
                <main>{children}</main>
                <footer style={{
                    background: `rebeccapurple`,
                    color: `white`,
                    paddingRight: "1.0875rem",
                    height: `auto`,
                }}>
                    @{new Date().getFullYear()}, Built with {`JS-Ng`}
                        <a href="/">
                            <img
                                style={{
                                    width: `1.75rem`,
                                    height: `1.75rem`,
                                    borderRadius: `50%`,
                                    float: "right",
                                }}
                                alt="Personal Icon"
                                src="https://pbs.twimg.com/profile_images/1245573441639428102/lMnoyJWs_400x400.jpg"
                                />
                        </a>
                </footer>
            </div>
        </>
    )
}

export default Layout;